<template>
  <div style="width:100%; height:100%; display:block; background:#fff;">
    <MobileHeader
      title="비밀번호 찾기"
      right-icon="mdi-help-circle-outline"
      @close="routingBack"
    />
    <div id="m_search-id">
      <section v-if="!certify">
        <StepDesc desc1="아이디와 휴대폰 번호를" desc2="입력해 주세요" />

        <section>
          <div id="certify-number">
            <div>
              <v-text-field
                v-model="member.id"
                type="text"
                color="main"
                placeholder="아이디를 입력해주세요"
              />
            </div>
            <div class="phone-box">
              <v-text-field
                v-model="member.memberPhone"
                type="text"
                color="main"
                placeholder="휴대폰 번호"
                maxlength="11"
                @input="changePhone"
                @keyup.enter="certifyTrans"
              >
                <template #append>
                  <button @click="certifyTrans">
                    {{ certifyTransFlag ? "다시요청" : "인증요청" }}
                  </button>
                </template></v-text-field
              >
            </div>
            <div v-if="certifyTransFlag" class="address">
              <!-- <div class="address"> -->
              <v-text-field
                v-model="certifyNumber"
                type="text"
                placeholder="인증번호 6자리"
                maxlength="6"
                color="main"
              >
                <template slot="append">
                  <p style="font-size: 14px" class="mb-0">{{ TimerStr }}</p>
                </template>
              </v-text-field>
            </div>
          </div>
        </section>

        <div
          id="next-btn__box"
          :class="{ active: certifyNumber && certifyNumber.length === 6 }"
        >
          <button @click="certifyCertify">
            다음
          </button>
        </div>
      </section>
      <section v-else>
        <StepDesc desc1="비밀번호를" desc2="다시 설정해 주세요" />
        <v-text-field
          v-model="member.newPassword"
          color="main"
          :append-icon="show1 ? 'mdi-eye-outline' : 'mdi-eye-off-outline'"
          :type="show1 ? 'text' : 'password'"
          placeholder="비밀번호(10 ~ 20자리 이내)"
          required
          @click:append="show1 = !show1"
        />

        <v-text-field
          v-model="newRePassword"
          color="main"
          :append-icon="show1 ? 'mdi-eye-outline' : 'mdi-eye-off-outline'"
          :type="show1 ? 'text' : 'password'"
          placeholder="비밀번호 확인"
          required
          @click:append="show1 = !show1"
        />
        <!-- 
        <NextButton
          :btn-active="passwordActive"
          :class="{ active: passwordActive }"
          @next="changePassword"
        /> -->

        <div
          id="next-btn__box"
          :class="{ active: member.newPassword === newRePassword }"
        >
          <button @click="submit">
            완료
          </button>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
import "@/assets/js/jquery-1.9.1.min.js";
import "@/assets/js/ux_common.js";
import myMixin from "@/mixin/mixin";
import { mapGetters } from "vuex";
import MobileHeader from "../../components/common/MobileHeader.vue";
import StepDesc from "../../components/common/StepDesc.vue";
export default {
  name: "SearchId",
  mixins: [myMixin],
  components: { MobileHeader, StepDesc },
  data() {
    return {
      member: {
        id: "",
        password: "",
        newPassword: ""
      },
      show1: false,
      newRePassword: "",
      certifyBeforeNumber: "",
      certifyTransFlag: false,
      certifyCertifyFlag: false,
      certifyNumber: "",
      certify: false,
      Timer: null,
      TimeCounter: 180,
      TimerStr: "03:00"
    };
  },
  props: {},
  computed: {
    ...mapGetters(["getMemberIdSearchList"])
  },
  mounted() {
    //let recaptchaScript = document.createElement('script')
    //recaptchaScript.setAttribute('src', 'https://www.google.com/recaptcha/api.js')
    //document.head.appendChild(recaptchaScript)
  },
  beforeDestroy() {
    this.$store.commit("MEMBER_SEARCH_INIT");
  },
  methods: {
    routingBack() {
      this.$router.go(-1);
    },
    move(path) {
      this.$router.push(path);
    },
    changePhone() {
      this.certifyTransFlag = false;
      this.certifyBeforeNumber = "";
      this.certifyCertifyFlag = false;
    },
    certifyCertify() {
      if (!this.certifyNumber) return;
      if (this.certifyBeforeNumber + "" !== this.certifyNumber + "") {
        alert("인증번호가 일치하지 않습니다.");
      } else {
        this.certifyCertifyFlag = true;
        alert("인증되었습니다.");
        this.certify = true;
      }
    },
    certifyTrans() {
      let result = Math.floor(Math.random() * 1000000) + 100000;
      if (result > 1000000) {
        result = result - 100000;
      }
      if (!this.phoneCheck(this.member.memberPhone)) {
        return;
      }

      this.certifyBeforeNumber = result;

      const parameter = {
        param: {
          phone: this.member.memberPhone,
          certifyNumber: this.certifyBeforeNumber
        }
      };
      this.$store.dispatch("REQUEST_MEMBER_CERTIFY", parameter).then(data => {
        if (data.result) {
          alert("인증번호를 전송하였습니다.");
          this.certifyTransFlag = true;

          if (this.Timer !== null) {
            this.timerStop(this.Timer);
            this.Timer = null;
          }
          this.Timer = this.timerStart();
        }
      });
    },
    validataion() {
      if (!this.certifyTransFlag && !this.certifyCertifyFlag) {
        alert("인증을 진행해 주세요.");
        return;
      }

      return true;
    },
    submit() {
      if (!this.validataion()) {
        return;
      }
      if (this.member.newPassword === null || this.member.newPassword === "") {
        alert("새 비밀번호를 입력하세요");
        return;
      }
      if (this.member.newPassword !== this.newRePassword) {
        alert("암호를 다시 확인하세요");
        return;
      }
      const parameter = {
        id: this.member.id,
        password: this.member.password,
        newPassword: this.member.newPassword
      };
      this.$store.dispatch("REQUEST_CHANGE_PASSWORD2", parameter);
    },
    timerStart() {
      this.TimeCounter = 180;
      const interval = setInterval(() => {
        this.TimeCounter--;
        this.TimerStr = this.prettyTime();
        if (this.TimeCounter <= 0) this.timerStop(interval);
      }, 1000);
    },
    timerStop(timer) {
      clearInterval(timer);
      this.TimeCounter = 0;
    },
    prettyTime() {
      const time = this.TimeCounter / 60;
      const minutes = parseInt(time);
      const secondes = Math.round((time - minutes) * 60);
      return `${minutes
        .toString()
        .padStart(2, "0")} : ${secondes.toString().padStart(2, "0")}`;
    }
  }
};
</script>
